.wrapper{
    display: flex;
    align-items: start;
    text-decoration: none;
    padding: 12px 16px;
}
.profilePictureWrapper{
    width: 40px;
    height: 40px;
    margin-right: 12px;
}
.profilePicture{
    aspect-ratio: 1/1;
    border-radius: 9999px;
    width: 40px;
    height: 40px;
}
.name, .status{
    color: var(--primary-text-color);
    font-size: 15px;
    line-height: 20px;
    font-weight: 700;
}
.name:hover{
    text-decoration: underline;
}
.status{
    font-weight: 400;
    overflow: hidden;
    white-space: normal;
    word-wrap: break-word;
}
.status span{
    display: inline-block;
    word-break: break-word;
    overflow: hidden;
}
